html {
  font-size: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 1rem;
}
@media (min-width: 40em) {
  body {
    height: 100vh;
    display: grid;
    place-content: center;
  }
}

.grid {
  display: grid;
  gap: 4.375rem;
}
@media (min-width: 40em) {
  .grid {
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
  }
}